@import url(//fonts.googleapis.com/earlyaccess/nanumgothic.css);

.nanumgothic * {
 font-family: 'Nanum Gothic', sans-serif;
}
body{
  height: 100%;
  overflow: scroll;
}
div{
  box-sizing: border-box;
}
.App{
  height: 100vh;
}
button{
  cursor: pointer;
  background-color: rgb(228, 76, 76);
  color: white;
  border: none;
  border-radius: 5px;
  font-weight: bold;
}
button:hover{
  background-color: rgb(167, 56, 56);;
}

.main{
  width: 50%;
  min-width: 300px;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
}

.main-img{
  position: relative;
  float: left;
  width: 50%;
}

.img-overlay{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  font-size: 50px;
  font-weight: bold;
  color: white;
  transition: 0.5s all;
}

.img-overlay:hover{
  background-color: rgba(0,0,0,0.5);
}

.main-img img{
  width: 100%;
  margin-bottom: -5px;
}

.main-game{
  position: relative;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  height: 200px;
  overflow: hidden;
}
.main-game img{
  width: 100%;
  margin-bottom: -5px;
}

.content{
  width: 100%;
}

.search{
  width: 100%;
  height: 120px;
  text-align: center;
  padding-top: 40px;
}

.search-container{
  margin-left: auto;
  margin-right: auto;
  min-width: 350px;
  display: block;
  width: 20%;
  position: relative;
  transition: 0.5s all;
}

.search-input{
  width: 100%;
  min-width: 350px;
  height: 40px;
  padding-left: 10px;
  font-size: 15px;
  box-sizing: border-box;
  border-radius: 5px;
  border: 2px solid rgb(228, 76, 76);
  outline: none;
  transition: 1s all;
}
.search-container:hover{
  width: 40%;
  transition: 0.5s all;
}

.search-btn{
  position: absolute;
  width: 50px;
  height: 40px;
  right: 0px;
  top: 0px;
  font-size: 13px;
  margin-left: -10px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  border: none;
  color: white;
  background-color: rgb(228, 76, 76);
}
.search-light input{
  background-color: rgb(58, 58, 58);
  color: white;
}
.search-light input::placeholder{
  color: white;
}
.search-btn:hover{
  color: lightgray;
  background-color: rgb(167, 56, 56);
}

@keyframes barrelroll{
  0% {
    transform: rotate(0deg);
  }
  25%{
    transform: rotate(90deg);
  }
  50%{
    transform: rotate(180deg);
  }
  75%{
    transform: rotate(270deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

.start{
  opacity: 0;
}
.end{
  opacity: 1;
  transition: all 0.5s;
}


@media screen and (max-width: 1199px) {
  .navbar-logo{
    margin:5px 20px 0px 5px;
  }
  .navbar-item{
    margin: 15px 10px 0px 0px;
  }
  .banner-l{
    display: none;
  }
  .banner-r{
    display: none;
  }
  .board{
    width: 95%;
  }
  .board-homeBtn{
    width: 100%;
    margin-top: 20px;
  }
  .board-newBtn{
    width: 100%;
    margin-top: 5px;
  }
  .detail-container{
    width: 95%;
  }
  .login-bg{
    width: 80%;
    left: 10%;
    right: 10%;
  }
  .main{
    width: 70%;
  }
  .main-img{
    width: 50%;
  }
  .img-overlay{
    font-size: 25px;
  }
  .main-game{
    width: 100%;
    height: 10%;
  }
  .edit-container{
    width: 90%;
  }
  .edit-btn button{
    width: 100%;
    margin-bottom: 5px;
  }
  .edit-btn{
    width: 100%;
  }
  .board-pc{
    display: none;
  }
  .board-m{
    display: block;
  }
  .detail-backBtn{
    width: 100%;
  }
  .detail-modifyBtn{
    width: 100%;
  }
  .detail-removeBtn{
    width: 100%;
  }
  .gacha-container{
    width: 75%;
    height: 500px;
  }
  .gacha-btns{
    flex-direction: column;
    margin: 10px 12.5% 0px 12.5%;
  }
  .gacha-result p{
    font-size: 20px;
  }
  .mypage-bg{
    width: 100%;
    margin: 0px;
    border: 10px solid red;
  }
}

@media screen and (max-width: 544px) {
  .gacha-container{
    width: 80%;
    height: 250px;
  }
  .gacha-btns{
    flex-direction: column;
    margin: 10px 10% 0px 10%;
  }
  .gacha-result p{
    font-size: 12px;
  }
  .navbar-r{
    font-size: 12px;
    margin-right: 0px;
    width: 125px;
    margin-top: 5px;
  }
}
.navbar-dark{
  background-color: rgb(228, 76, 76);
}
.navbar-light{
  background-color: rgb(167, 56, 56);
  color: lightgray;
}

.main-light{
  background-color: rgba(0,0,0,0.3);
  color: lightgray;
}
.main-light:hover{
  background-color: rgba(0,0,0,0.7);
}